<template>
    <div class="edit" v-show="showedit">
        <form class="form-horizontal">
            <div class="form-group" >
                <label for="exampleInputName2">Name/</label>
                <input type="text" class="form-control" id="exampleInputName2" name="name" placeholder="">
            </div>
            <div class="form-group">
                <label for="exampleInputEmail2">Email/</label>
                <input type="email" class="form-control" id="exampleInputEmail2" name="email" placeholder="">
            </div>
            <div class="form-group">
                <label for="exampleInputphone2">phone/</label>
                <input type="phone" class="form-control" id="exampleInputphone2" name="phone" placeholder="">
            </div>
            <div class="form-group">
                <label for="exampleInputmoney2">money/</label>
                <input type="type" class="form-control" id="exampleInputmoney2" name="money" placeholder="">
            </div>
            <div class="form-group">
                <label for="exampleInputmoney2"></label>
                    <select class="form-control">
                    <option value="true"></option>
                    <option value="false"></option>
                </select>          
            </div>   
         </form>
         <p>
            <button type="button" class="btn btn-primary " @click="cancel"></button>
            <button type="button" class="btn btn-default" @click="confirm"></button>
        </p>
    </div>
</template>
<style lang="scss" scoped>
// 
.edit{
    margin-left:15px;
    margin-top:30px;
    min-height:400px;
}
</style>
<script>
export default {
    props: ['showedit'],
    methods:{
        cancel(){//
            this.$emit('fnshowedit',this.showedit=true);
        },
        confirm(){
          let name = $("input[name='name']").val();//
          let email = $("input[name='email']").val();//
          let phone = $("input[name='phone']").val();//
          let money = $("input[name='money']").val();//
          let control=$('.form-control option:selected').val();
          if(name==''){
              alert('');
              return false;
          }
          if(email==""){
              alert('');
              return false;
          }else{
               var regemail = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/;
               var  isemail= regemail.test(email )
               if(!isemail){
                alert("");
                 $("input[name='email']").focus();
                 return false;
               }
          }
          if(phone==''){
              alert('');
              return false;
          }else{
              var regphone=/^[1][3,4,5,7,8][0-9]{9}$/;
              var isphone = regphone.test(phone) ;
              if(!isphone){
                   alert("");
                  $("input[name='phone']").focus();
                  return false;
              }
          }
          if(money==''){
              alert('');
              return false;
          }else{
              if( parseInt(money)<=0){
                  alert('1');
                    return false;
              }
            
          }
          if(control=='false'){
              control=false
          }else if(control=='true'){
              control=true
          }
            var newUser={};
            newUser.name=name;
            newUser.emil =email;
            newUser.phone =phone;
            newUser.money =money;
            newUser.state=control;
            newUser.img='https://s3.amazonaws.com/uifaces/faces/twitter/okansurreel/128.jpg';
            newUser.pro=Math.floor((money/10));
             this.$emit('newuser',newUser);
             this.$emit('fnshowedit',this.showedit=true);
        }
    }
}
</script>



