<template>
  <div>
       <!-- ========================== -->
       <div class="container my_container" >
          <!-- Nav tabs -->
            <ul class="nav nav-tabs" role="tablist">
                <li role="presentation" class="active"><a href="#home" aria-controls="home" role="tab" data-toggle="tab"></a></li>
                <li role="presentation"><a href="#profile" aria-controls="profile" role="tab" data-toggle="tab"></a></li>
            </ul>
            <div class="tab-content">
                <!-- tab    -->
               <user-info></user-info>
                 <!-- tab  -->
               <entrystatus></entrystatus>
                <!-- tab  -->
                <div role="tabpanel" class="tab-pane" id="messages">...</div>
                <div role="tabpanel" class="tab-pane" id="settings">...</div>
            </div>
              
       </div>
       <!-- ========================== -->

  </div>
</template>
<script>
import Vue from "vue";
import userInfo from './comminity/userInfo';//
import entrystatus from './comminity/user_entry_status';//
export default{
       components: {
        userInfo,
        entrystatus
    },
};
</script>

<style scoped>
.my_container{
    background:#fff;
    margin-top:20px;
    padding-top:20px;
    padding-bottom:10px;
    margin-bottom:20px;
}
p{
    /* font-size:25px; */
 
    padding:15px 0;

}
table th{
       text-align:center; 
}
table{
    margin-top:30px;
    text-align:center;
}
table tr:nth-child(odd){background:#f2f6f9;}
table tr:nth-child(even){background:#e4eaf1;}
.tabBar{
    margin-right:0px;
}
</style>





